:root {
  --background-color: #fff;
  --text-color: #2d2d2d;
  --secondary-text-color: #666;
  --accent-color-lavender: #f9d1f1;
  --accent-color-pink: #ffccd5;
  --accent-color-mint: #c9f7df;
  --accent-color-yellow: #fff0cc;
  --accent-color-blue: #c6e4ff;
  --link-color: #9a97ea;
  --border-color: #f0f0f0;
  --font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
  --section-spacing: 6rem;
  --content-max-width: 1000px;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-family);
  background-color: var(--background-color);
  color: var(--text-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor: none;
  margin: 0;
  padding: 0;
  line-height: 1.6;
}

h1, h2, h3 {
  color: var(--text-color);
  margin-top: 0;
  font-weight: 500;
}

h1 {
  margin-bottom: .5rem;
  font-size: 3rem;
  font-weight: 700;
}

h2 {
  border-bottom: 1px solid #e0e0e0;
  margin-bottom: 1.5rem;
  padding-bottom: .5rem;
  font-size: 1.8rem;
}

h3 {
  margin-bottom: .25rem;
  font-size: 1.2rem;
}

p {
  color: var(--secondary-text-color);
  margin-bottom: 1rem;
}

a {
  color: var(--link-color);
  text-decoration: none;
  transition: color .3s;
}

a:hover {
  color: #7a78c8;
}

ul {
  padding-left: 0;
  list-style: none;
}

main {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 2rem;
}

.content-section {
  opacity: 0;
  padding: 4rem 0;
  transition: opacity .6s ease-out, transform .6s ease-out;
  transform: translateY(20px);
}

.content-section.visible {
  opacity: 1;
  transform: translateY(0);
}

.main-header {
  -webkit-backdrop-filter: blur(10px);
  border-bottom: 1px solid var(--border-color);
  z-index: 1000;
  background-color: #fffffff2;
  width: 100%;
  padding: 1rem 0;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 2px 10px #0000000d;
}

.main-header nav ul {
  justify-content: center;
  gap: 2rem;
  margin: 0;
  padding: 0;
  display: flex;
}

.main-header nav li {
  margin: 0 1.5rem;
}

.main-header nav a {
  color: var(--text-color);
  font-weight: 500;
  transition: color .3s;
  position: relative;
}

.main-header nav a:after {
  content: "";
  background: var(--link-color);
  width: 0;
  height: 2px;
  transition: width .3s;
  position: absolute;
  bottom: -2px;
  left: 0;
}

.main-header nav a:hover:after {
  width: 100%;
}

.hero-section {
  text-align: center;
  opacity: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
  padding: 4rem 0;
  animation: 1s ease-out forwards fadeIn;
  display: flex;
  position: relative;
  overflow: hidden;
}

.hero-content {
  align-items: center;
  gap: 2rem;
  margin-bottom: 1rem;
  display: flex;
}

.hero-headshot {
  object-fit: cover;
  border: 3px solid var(--accent-color-lavender);
  border-radius: 50%;
  width: 120px;
  height: 120px;
  box-shadow: 0 4px 10px #0000001a;
}

.gradient-name {
  color: #0000;
  letter-spacing: -.02em;
  background: linear-gradient(45deg, #4cc9f0, #7209b7, #f72585, #ff0a54) 0 0 / 300% 300% text;
  margin: 0;
  font-size: 4.5rem;
  font-weight: 700;
  animation: 4s infinite gradientShift;
}

@keyframes gradientShift {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

.hero-section p {
  color: var(--secondary-text-color);
  margin-top: 1rem;
  font-size: 1.2rem;
}

.experience-item {
  border-left: 2px solid var(--border-color);
  background: #fffc;
  border-radius: 15px;
  margin-bottom: 2.5rem;
  padding: 2rem 2rem 2rem 1.5rem;
  transition: transform .3s, box-shadow .3s;
  box-shadow: 0 5px 15px #0000000d;
}

.experience-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px #0000001a;
}

.experience-item:nth-child(5n+1), .project-item:nth-child(5n+1) {
  border-left-color: #4cc9f0;
}

.experience-item:nth-child(5n+2), .project-item:nth-child(5n+2) {
  border-left-color: #7209b7;
}

.experience-item:nth-child(5n+3), .project-item:nth-child(5n+3) {
  border-left-color: #f72585;
}

.experience-item:nth-child(5n+4), .project-item:nth-child(5n+4) {
  border-left-color: #ff0a54;
}

.experience-item:nth-child(5n), .project-item:nth-child(5n) {
  border-left-color: #4cc9f0;
}

.experience-item h3 {
  font-weight: 500;
}

.experience-item .date {
  color: var(--secondary-text-color);
  margin-bottom: .5rem;
  font-size: .9rem;
}

.experience-item ul {
  padding-left: 1rem;
}

.experience-item li {
  color: var(--secondary-text-color);
  margin-bottom: .5rem;
  padding-left: 15px;
  position: relative;
}

.experience-item li:before {
  content: "â€¢";
  color: var(--link-color);
  font-weight: bold;
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
}

.button {
  color: #fff;
  background: linear-gradient(45deg, #4cc9f0, #7209b7, #f72585, #ff0a54) 0 0 / 300% 300%;
  border-radius: 20px;
  margin-top: 1rem;
  padding: .8rem 1.5rem;
  font-weight: 500;
  transition: all .3s;
  animation: 4s infinite gradientShift;
  display: inline-block;
  box-shadow: 0 2px 8px #0000001a;
}

.button:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px #0003;
}

#contact p {
  color: var(--text-color);
}

#contact p a:before {
  content: "";
  border-radius: 50%;
  width: 8px;
  height: 8px;
  margin-right: 8px;
  display: inline-block;
}

#contact p a:first-of-type:before {
  background-color: var(--accent-color-pink);
}

#contact p a:nth-of-type(2):before {
  background-color: var(--accent-color-mint);
}

#contact p a:nth-of-type(3):before {
  background-color: var(--accent-color-yellow);
}

#contact p a:nth-of-type(4):before {
  background-color: var(--accent-color-blue);
}

.main-footer {
  text-align: center;
  border-top: 1px solid var(--border-color);
  margin-top: 4rem;
  padding: 2rem 0;
  font-size: .9rem;
}

.main-footer p {
  color: #000;
  background: none;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@media (width <= 768px) {
  h1 {
    font-size: 2.5rem;
  }

  .hero-section h1 {
    font-size: 3rem;
  }

  main {
    margin: 2rem auto;
    padding: 0 1rem;
  }

  .content-section {
    padding: 3rem 0;
  }

  .main-header nav li {
    margin: 0 1rem;
  }

  :root {
    --section-spacing: 4rem;
  }

  .gradient-name {
    font-size: 3.5rem;
  }

  .about-container {
    flex-direction: column;
    gap: 2rem;
    padding: 2rem;
  }

  .main-header nav ul {
    gap: 1rem;
  }
}

@media (width <= 480px) {
  h1 {
    font-size: 2rem;
  }

  .hero-section h1 {
    font-size: 2.5rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  .main-header nav ul {
    justify-content: space-around;
  }

  .main-header nav li {
    margin: 0 .5rem;
  }
}

.cursor-dot, .cursor-outline {
  pointer-events: none;
  z-index: 9999;
  opacity: 1;
  border-radius: 50%;
  transition: transform .15s, opacity .3s;
  position: fixed;
  top: 0;
  left: 0;
  transform: translate(-50%, -50%);
}

.cursor-dot {
  background-color: #f72585;
  width: 8px;
  height: 8px;
}

.cursor-outline {
  background-color: #0000;
  border: 2px solid #f72585;
  width: 30px;
  height: 30px;
}

.cursor-dot.active {
  opacity: .8;
  transform: translate(-50%, -50%)scale(1.5);
}

.cursor-outline.active {
  background-color: #9a97ea1a;
  transform: translate(-50%, -50%)scale(.7);
}

@media (width <= 768px) {
  .cursor-dot, .cursor-outline {
    display: none;
  }

  body {
    cursor: auto;
  }
}

.project-item {
  border-left: 2px solid var(--border-color);
  background: #fffc;
  border-radius: 15px;
  margin-bottom: 2.5rem;
  padding: 2rem 2rem 2rem 1.5rem;
  transition: transform .3s, box-shadow .3s;
  box-shadow: 0 5px 15px #0000000d;
}

.project-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px #0000001a;
}

.project-item h3 {
  margin-bottom: .5rem;
  font-weight: 500;
}

.project-item p {
  color: var(--secondary-text-color);
}

.about-container {
  background: #fffc;
  border-radius: 20px;
  align-items: center;
  gap: 3rem;
  padding: 3rem;
  display: flex;
  box-shadow: 0 10px 30px #0000000d;
}

.about-container p {
  flex: 1;
  min-width: 300px;
  margin: 0;
}

.headshot {
  object-fit: cover;
  border-radius: 12px;
  width: 200px;
  height: 200px;
  box-shadow: 0 4px 10px #0000000d;
}

@media (width <= 768px) {
  .about-container {
    flex-direction: column;
    gap: 2rem;
    padding: 2rem;
  }

  .about-container p {
    min-width: 100%;
  }
}

#contact p a {
  color: var(--text-color);
  background: none;
  font-weight: 500;
  display: inline-block;
}

#contact p a:hover {
  opacity: .8;
}
